import React, { useState } from 'react'
import { Link, useLocation } from 'react-router-dom'
import { MenuOutlined, UserOutlined, LogoutOutlined, ShoppingCartOutlined } from '@ant-design/icons'
import { Input, Space, Avatar, Dropdown } from 'antd';
const { Search } = Input

const navLinks = [
  { title: 'Home', path: '/' },
  { title: 'Men', path: '/men' },
  { title: 'Women', path: '/women' },
  { title: 'Contact', path: '/contact' },
]

const items = [
  {
    key: '1',
    label: (
      <div className="flex items-center">
        <UserOutlined />
        <span className="ml-4">Profile</span>
      </div>
    ),
  },
  {
    key: '2',
    label: (
      <div className="flex items-center">
        <UserOutlined />
        <span className="ml-4">Profile</span>
      </div>
    ),
  },
  {
    key: '3',
    danger: true,
    label: (
      <div className="flex items-center">
        <LogoutOutlined />
        <span className="ml-4">Logout</span>
      </div>
    ),
  },
]

const Navbar = () => {
  const location = useLocation()

  const [isMenuOpen, setIsMenuOpen] = useState(false)

  return (
    <div>
      <nav className="lg:px-16 px-6 bg-white shadow-md flex flex-col md:flex-row md:flex-wrap items-center lg:justify-between pb-3 lg:pb-0">
        <div className="w-full md:w-max flex gap-6 items-center">
          <div className="cursor-pointer md:hidden block" onClick={() => setIsMenuOpen(!isMenuOpen)}>
            <MenuOutlined />
          </div>
          <Link to="/">
            <h1 className='text-3xl font-extrabold italic py-4'>Ambitious</h1>
          </Link>
          <div className='ml-auto flex items-center gap-4 md:hidden'>
            <Dropdown menu={{ items, }}>
              <Avatar icon={<UserOutlined />} />
            </Dropdown>

            <Link to='/cart' className='text-2xl'><ShoppingCartOutlined /></Link>
          </div>
        </div>

        <div className={`lg:flex lg:items-center md:w-auto lg:order-last w-full ${isMenuOpen ? 'max-sm:h-52' : 'max-sm:h-0'} md:ml-auto lg:ml-0 overflow-hidden duration-300`}>
          <nav className='flex gap-5'>
            <ul className="text-xl text-center items-center gap-4 pt-4 md:gap-8 lg:text-lg flex flex-col md:flex-row md:pt-0">
              {navLinks.map((link, index) => (
                <li key={index} className={`${location.pathname == link.path ? 'text-black font-bold' : 'text-slate-600'}`}>
                  <Link to={link.path}>{link.title}</Link>
                </li>
              ))}
            </ul>

            <div className='ml-5 flex items-center gap-4 max-sm:hidden'>
              <Dropdown menu={{ items, }}>
                <Avatar icon={<UserOutlined />} className='cursor-pointer' />
              </Dropdown>

              <Link to='/cart' className='text-2xl'><ShoppingCartOutlined /></Link>
            </div>
          </nav>
        </div>

        <div className=' w-full lg:w-96'>
          <Search
            placeholder="input search text"
            onSearch={value => console.log(value)}
            enterButton
            size="large" />
        </div>
      </nav>
    </div>
  )
}

export default Navbar