import React from 'react'

const ProductCard = ({ product }) => {
  return (
    <div className='max-w-72 flex flex-col rounded-lg border shadow hover:scale-[103%] hover:shadow-lg duration-300 overflow-hidden'>
      <img src={product.image} className='w-full h-44 lg:h-60 object-contain' />
      <div className='p-4'>
        <h3 className='text-xl font-bold line-clamp-2'>{product.title}</h3>
        <p className='my-2 line-clamp-3'>{product.description}</p>
        <h3 className='text-xl font-bold text-slate-800'>$ {product.price}</h3>
      </div>
    </div>
  )
}

export default ProductCard